.navBar{
    height: 50px;
    border-bottom: 0.5px solid  rgb(231, 228, 228);
    display: flex;
    align-items: center;
    font-size: 14px;
    color: #555;

        .wrapper{
            width: 100%;
            padding: 20px;
            display: flex;
            align-items: center;
            justify-content: space-between;

            .search{
                display: flex;
                align-items: center;
                border: 0.5px solid lightgray;
                padding: 3px;
               
                input{
                    border: none;
                    outline: none;
                    background-color: transparent;
                    border-radius: .5rem !important; 
                    padding: .1rem .5rem;
                    &::placeholder{
                        font-size: 12px;
                    }
                }
            }
        }
        .items{
            display: flex;
            text-align: center;

            .item{
                display: flex;
                align-items: center;
                margin-right: 20px;
                position: relative;
                .darkBtn{
                    background-color: transparent;
                    border: none;
                    outline: none;
                    color: #000;

                }
                    .icon{
                        font-size: 20px;
                        cursor: pointer;
                    }
                    .avatar{
                        width: 30px;
                        height: 30px;
                        border-radius: 50%;
                    }
                    .counter{
                        width: 15px;
                        height: 15px;
                        background-color: red;
                        border-radius:50% ;
                        color: #fff;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        font-size: 10px;
                        font-weight: bold;
                        position: absolute;
                        top: -5px;
                        right: -5px;
                    }
                }
        }

}